
.nav-list {
  margin: 20px 0px;
  width: 100%;
  flex-wrap: wrap;
  align-items: center;
}

.nav-list li {
  height: 90px;
  background-color: rgb(192, 129, 58);
  font-size: 20px;
  text-align: center;
  border: 1px solid black;
  padding-top: 25px;
}

.nav-list li:hover,
.nav-list li.active {
  background-color: rgba(255, 255, 255, 0.507);
  box-shadow: 1px 1px 1px #797676;
  color: black;
  cursor: pointer
}
