.nav-content {
  background: #ffffff75;
  font-size: 30px;
  border: 2px solid black;
  margin: 15px;
}
.nav-content__input[type=number]:focus {
  border: none
}

.nav-content__input[type=number] {
  background: transparent;
  width: 60px;
  font-size: 25px;
  border: none; /*2px solid rgb(148, 132, 132);*/
  padding: 5px;
}

.nav-content__input[type=range] {
  width: 100%;
  height: 32px;
  cursor: pointer;
  box-shadow: 0.1px 0.1px 0px #005900, 0px 0px 0.1px #007200;
  background: #0000ff;
  border: 0px solid #010101;
}
